.c_password-meter
{
    margin-bottom: 10px;
    height: 10px;
    width: 50%;
    background-color: #22272a;
    > div
    {
        height: 10px;
        transition: .4s;
    }
    .none
    {
        width: 0;
    }
    .risk
    {
        width: 20%;
        background-color: rgba(220, 0, 0, .7);
    }
    .weak
    {
        width: 40%;
        background-color: rgba(255, 140, 0, .8);
    }
    .normal
    {
        width: 60%;
        background-color: rgba(255, 255, 0, .7);
    }
    .safly
    {
        width: 80%;
        background-color: rgba(173, 255, 47, .7);
    }
    .strong
    {
        width: 100%;
        background-color: rgba(0, 128, 0, .7);
    }
}