@import '../../constants/variable';

div.c_modal
{
    position: fixed;
    z-index: 100;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    opacity: 0;
    background-color: rgba(0, 0, 0, .65);
    display: flex;
    justify-content: center;
    align-items: center;
    color: $modal-gray;

    > div.content
    {
        display: flex;
        flex-direction: column;
        width: 600px;
        min-height: 400px;
        opacity: 1;
        background-color: #12172a;
        border: solid 1px #4a4a4a;
        font-size: 14px;

        @include rwd(ipad)
        {
            width: 100%;
        }
        
        > div.head
        {
            display: flex;
            justify-content: flex-end;
            height: 48px;

            > div
            {
                width: 48px;
                color: $modal-gray;
                background-color: #000;
                display: flex;
                justify-content: center;
                align-items: center;
                cursor: pointer;

                img
                {
                    width: 24px;
                    height: 24px;
                }
            }
        }

        > div.content
        {
            flex: 1;
            width: 76%;
            margin: 0 auto;
            padding: 15px;
            display: flex;

            @include rwd(ipad)
            {
                width: 90%;
                padding: 15px 0;
            }

            .modal-title
            {
                font-size: 1.5em;
                color: #fff;
                margin-bottom: 32px;
            }

            > div
            {
                display: flex;
                flex-direction: column;
                flex: 1;
                width: 100%;
            }

            .modal-content
            {
                > div
                {
                    margin-bottom: 32px;
                    display: flex;
                    align-items: center;

                    > div:first-child
                    {
                        margin-right: 24px;
                        min-width: 100px;
                    }
                }

                .meter
                {
                    flex-direction: column;
                }

                p
                {
                    margin: 0;
                }

                .warning
                {
                    color: $main-red;
                }

                .cancel-item
                {
                    margin: 12px 0;
                    font-size: 16px;
                }

                .meter
                {
                    .c_password-meter
                    {
                        width: 100%;
                        margin-right: 0;
                    }
                }

                .modal-input
                {
                    display: flex;
                    
                    label
                    {
                        margin-right: 24px;
                    }

                    > div
                    {   
                        flex: 1;
                        input
                        {
                            background-color: transparent;
                            border: 1px solid $font-gray;
                            width: calc(100% - 18px);
                            outline: none;
                            padding: 7px 8px;
                            color: $font-purple;
                            -webkit-text-fill-color: $modal-gray;
                            border-radius: 2px;
                        }

                        &.error
                        {
                            position: relative;
    
                            &::after
                            {
                                position: absolute;
                                content: attr(data-message);
                                color: $main-red;
                                font-size: 11px;
                                top: 36px;
                                right: 0;
                            }

                            input 
                            {
                                border-color: $main-red;
                            }
                        }
                    }
                }

                > div.detail
                {
                    > div
                    {
                        width: calc(100% - 102px);

                        > div
                        {
                            margin-bottom: $div-gap;
                        }
                    }

                    img
                    {
                        width: 102px;
                        transform: translateY(20px);
                    }
                }

                input[type="checkbox"]
                {
                    background-color: transparent;
                }

                .canvas
                {
                    height: 132px;
                    width: 132px;
                    background-color: #fff;
                    margin: 0 auto;
                    margin-bottom: 40px;

                    .qrcode
                    {
                        padding: 6px;
                    }
                }

                .address
                {
                    > div:last-child
                    {
                        text-overflow: ellipsis;
                        white-space: nowrap;
                        overflow: hidden;
                        max-width: calc(100% - 124px);
                    }
                }

                .receipt-title
                {
                    font-size: 16px;
                    margin-bottom: 10px;
                }

                .receipt
                {
                    text-decoration: none;
                    color: #fff;
                    font-size: 12px;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    overflow: hidden;

                    &:hover
                    {
                        color: $main-green;
                    }
                }
            }

            .import
            {
                position: relative;
                padding: .5em 1em;
                border: solid 1px #4a4a4a;
                background-color: #000;
                text-align: center;
                max-width: 120px;
                cursor: pointer;
                overflow: hidden;
                display: inline-block;
                margin-bottom: 32px;

                .import-btn
                {
                    cursor: pointer;
                    width: 100%;
                    height: 100%;
                }
                
                .import-input
                {
                    position: absolute;
                    font-size: 100px;
                    right: 0;
                    top: 0;
                    opacity: 0;
                    cursor: pointer
                }
            }
        }

        .canvas-holder
        {
            display: flex;
            justify-content: center;
            margin: 30px 0;

            .qrcode
            {
                padding: 8px;
                background-color: #fff;
            }
        }

        .modal-btn
        {
            padding-bottom: 40px;
            display: flex;
            width: 80%;
            margin: 0 auto;
            justify-content: center;
            margin-top: auto;
            
            .button
            {
                @include button;
                min-width: 120px;

                &.margin-none
                {
                    margin: 0;
                }
            }

            .block
            {
                @include button;
                background-color: #4a4a4a;
                // pointer-events: none;
                color: $font-gray;
                border-color: $font-gray;
                cursor: not-allowed;

                &::after
                {
                    background-image: none;
                }

                &:hover
                {
                   border-color: $font-gray;
                   color: $font-gray;

                   span
                   {
                       color: $font-gray;
                   }
                }

                &.active
                {
                    color: $font-gray;
                    background-image: none;
                }
            }

            a
            {
                text-decoration: none;
            }

            .submit
            {
                color: #fff;
                padding: 7px 50px;
                border-radius: 4px;
                border: none;
                outline: none;
                font-size: 16px;
                cursor: pointer;

                &.buy
                {
                    background-color: $main-green;
                }

                &.sell
                {
                    background-color: $main-red;
                }
            }
        }
       
    }

    .button
    {
        @include button;
    }

    &.fadeIn
    {
        animation-name: fadeIn;
        animation-duration: .3s;
        animation-timing-function: linear;
        animation-fill-mode: both;
    }


    @keyframes fadeIn
    {
        from
        {
            opacity: 0;
        }

        to
        {
            opacity: 1;
        }
    }
}
