@import '../../constants/variable';

.c_loading
{
    position: relative;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    min-width: 200px;

    &.none
    {
        opacity: 0;
        transition: .4s;
        pointer-events: none;
    }
    position: fixed;
    opacity: 1;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    border-radius: 10px;
    background-color: rgba(0, 0, 0, .7);
    z-index: 9999;
    display: flex;
    justify-content: center;
    align-items: center;
    p
    {
        animation: pulse 2s linear infinite;
        color: #fff;
        margin: 10px;
    }
    .loader {
        position: relative;
        // width: 60px;
        // height: 60px;
        // border-radius: 50%;
        // display: inline-block;
        // vertical-align: middle;
        // border: 7px double $font-purple;
        width: 50px;
        height: 50px;
        background-image: url('/img/loading-circle.svg');
        background-repeat: no-repeat;
        background-size: cover;
        animation: ball-turn 1s linear infinite;
    }
    
    .loader:before,
    .loader:after {
        // content: "";
        position: absolute;
        width: 12px;
        height: 12px;
        background: $font-purple;
        border-radius: 50%;
        bottom: 0;
        right: 37px;
    }
    
    .loader:after {
        left: 37px;
        top: 0;
    }

    @keyframes pulse
    {
        0% {
        opacity: .25;
        }
        50% {
        opacity: 1;
        }
        100% {
        opacity: .25;
        }
    }

    @keyframes ball-turn {
        0% {
            transform: rotate(0deg);
        }
        100% {
            transform: rotate(360deg);
        }
    }
}


