@import '../../constants/variable';

.c_toast
{
    position: fixed;
    font-size: 16px;
    bottom: 16px;
    padding: 8px 16px;
    color: #fff;
    background-color: #000;
    opacity: 0;
    left: 50%;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: translateX(-50%);
    z-index: 9999;
    
    &.fadeIn
    {
        animation-name: in;
        animation-duration: 2.5s;
    }

    &::after
    {
        content: '';
        position: fixed;
        width: 100%;
        height: 100%;
        background-color: transparent;
        top: 0;
        left: 0;
    }
    
    @keyframes in {
        from
        {
            opacity: 0;
        }

        25%
        {
            opacity: 1;
            bottom: 36px;
        }

        75%
        {
            opacity: 1;
            bottom: 36px;
        }

        to
        {
            opacity: 0;
            bottom: 16px;
        }
    }
}